* {
    padding: 0px;
}

:root {
    --padding10px: 10px;
}
.wrapper {
    padding: var(--padding10px);
    margin: 10px;
    display: grid;
    grid-template-rows: 120px;
    grid-template-columns: 2fr 5fr;
}

.logo {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.imgLogo {
    width: 80px;
    height: 80px;
    border-radius: 50%;
}

.buttonFollow {
    width: fit-content;
    padding: 3px 10px 3px 10px;
    margin-top: 5px;
    border-radius: 3px;
    background-color: teal;
    color: white;
}

.content {
    display: grid;
    grid-template-rows: 100px;
    padding: 3px 10px 3px 10px;
    grid-template-columns: 5fr 5fr;
}

.nameStatus {
    display: flex;
    flex-direction: column;
    justify-content: space-around;
}

.countryCity {
    display: flex;
    flex-direction: column;
    align-items: flex-end;
    justify-content: space-around;
}

.contentBorder {
    border: 5px solid green;
    border-image: url(./../assets/border.png) 25 stretch;
    margin-left: 5px;
}

.buttonReadMore {
    width: 150px;
    height: 50px;
    border-radius: 15px;
    background-color: teal;
    color:white;
    font-size: 20px;
    justify-self: center;
}
.friendsContent{
    display: grid;
}
